@import url(https://fonts.googleapis.com/css?family=Krub);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Krub", sans-serif;
}

.App-header {
  color: #000000;
  text-align: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 46px;
  padding: 10px 0px 0px 50px;
  font-family: "Krub", sans-serif;
}

.Header-stroke {
  background-color: #56ccf2;
  height: 3px;
  margin: 10px 30px 10px 30px;
}

.App-body {
  text-align: center;
}

.Bracket-body {
  height: 440px;
  margin: 10px 100px 0px 100px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  border-right: 3px #000000;
}

.Bracket-round {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.Team-input {
  margin: 0px 50px 0px 50px;
  font-size: 16px;
  border: 0;
  text-align: center;
  font-family: "Krub", sans-serif;
}

.Team-input:focus {
  outline: none;
}

.Team-stroke {
  background-color: #000000;
  height: 3px;
  /* margin: 0px 10px 0px 10px; */
}

.Vertical-stroke {
  position: absolute;
  left: 28.75%;
  background-color: #000000;
  width: 2px;
  height: 55px;
}

.Vertical-stroke2 {
  position: absolute;
  left: 49.85%;
  background-color: #000000;
  width: 2px;
  height: 110px;
}

.Vertical-stroke3 {
  position: absolute;
  left: 70.95%;
  background-color: #000000;
  width: 2px;
  height: 220px;
}

.Win-prob {
  position: absolute;
  left: 38.4%;
}

.Win-prob2 {
  position: absolute;
  left: 59.6%;
}

.Win-prob3 {
  position: absolute;
  left: 80.8%;
}

.Bracket-button {
  text-align: right;
  padding: 0px 100px 0px 0px;
}

.Clear-button:focus {
  outline: none;
}

.Predict-button:focus {
  outline: none;
}

.Clear-button {
  cursor: pointer;
  margin-right: 20px;
  padding: 0px 10px 0px 10px;
  border-width: 4px;
  border-color: #56ccf2;
  border-radius: 20px;
  background: none;
  color: #000000;
  text-align: left;
  font-size: 20px;
  font-weight: bold;
  line-height: 46px;
  font-family: "Krub", sans-serif;
}

.Predict-button {
  cursor: pointer;
  padding: 0px 10px 0px 10px;
  border-width: 4px;
  border-color: #56ccf2;
  border-radius: 20px;
  background: none;
  color: #000000;
  text-align: left;
  font-size: 20px;
  font-weight: bold;
  line-height: 46px;
  font-family: "Krub", sans-serif;
}

